  font-size: 15px;
  line-height: 1;
  white-space: nowrap;
  transition: 0.3s;
}
.btn_secondary {
  color: #fbfbfb;
  background-image: linear-gradient(180deg, #fdcc75 0%, #fe672e 98%);
  background-size: 100% 200%;
  background-position-y: 100%;
  box-shadow: 0px 4px 24px 0px rgba(254, 103, 46, 0.5);
}
.btn_rounded {
  border-radius: 0.5em;
}
.btn_outline {
  color: #151d28;
  background-color: #fbfbfb;
  border: solid 1px transparent;
  background: linear-gradient(#FBFBFB 0 0) padding-box, linear-gradient(90deg, #151d28 0%, #151d28 100%) border-box;
}
.btn_circle {
  width: 24.4rem;
  height: 24.4rem;
  padding: 1.5em;
  font-size: 22px;
  font-weight: 400;
  color: #fbfbfb;
  background-image: linear-gradient(180deg, #fdcc75 0%, #fe672e 98%);
  box-shadow: 0px 4px 24px 0px rgba(244, 114, 19, 0.3);
  border-radius: 50%;
}

@media (hover: hover) and (pointer: fine) {
  .btn_outline:hover {
    color: #fe672e;
    background: linear-gradient(#FBFBFB 0 0) padding-box, linear-gradient(90deg, #fe672e 0%, #c90678 100%) border-box;
